<template>
    <div
        class="content"
        v-show="data.redPacketPicCash">
        <swipe-list
            theme="black"
            class="swipe"
            :list="data.invitationCarousel"
        />
        <div
            :class="{
                invitation__container: true,
                preinvited: preinvited,
        }">
            <div
                v-if="noWithdrawTip"
                @click="showNoWithdrawTipDialog"
                class="no-withdraw-tip scale">
                <i />
                <span>提现不了？</span>
            </div>
            <div
                v-if="!preinvited"
                class="refuser"
            >
                <img
                    class="invitation__headIcon"
                    :src="data.headIcon || headIcon">
                <div class="invitation__tip">
                    <p class="invitation-source">“{{ getShortName(data.user) }}”给你发来了现金红包</p>
                    <p
                        class="invitation-code"
                        v-if="invitationCode"
                    >Ta的邀请码：{{ showInvitationCode }}</p>
                </div>
            </div>
            <div
                v-if="data.redPacketPicCash"
                class="invitation__money"
            >
                {{ (+data.redPacketPicCash).toFixed(2) }}<span>元</span>
            </div>
            <div
                v-if="preinvited"
                class="preinvited-invitation-tip"
            >
                红包已到账，马上去提现
            </div>
            <div
                v-if="preinvited"
                class="preinvited-invitation-code"
            >
                Ta的邀请码：{{ showInvitationCode }}
            </div>
            <big-button
                v-if="isWechatBrowser() && !preinvited"
                class="wechat-login scale-infinite"
                @click="wehcatAuth"
            >微信登录 立即领取</big-button>
            <div
                v-if="!isWechatBrowser() && !preinvited"
                ref="phoneLogin"
                class="phone-login">
                <div class="phonenumber">
                    <i class="i-phone"/>
                    <input
                        ref="phonenumber"
                        v-model="phonenumber"
                        type="number"
                        @blur="telInputStatusHandler(false)"
                        @focus="telInputStatusHandler(true)"
                        placeholder="请输入您的手机号领取"
                    >
                    <i
                        v-if="phonenumberError"
                        class="i-phone-error heartBeat"/>
                </div>
                <big-button
                    :class="{'phone-login-btn': true, 'scale-infinite': telBtnActive }"
                    @click="submitPhoneNumber"
                >立即领取</big-button>
            </div>
            <div
                class="countdown-container"
                v-if="preinvited">
                <p>24小时后将过期，请尽快提现</p>
                <CountDown
                    :is-show-day="false"
                    :time="(24 * 60 * 60 * 1000) - 1000"
                    separator=":"
                    class="countdown"
                    :c-style="{
                        height: '26px',
                        color: 'rgba(104, 79, 52, 1)',
                        time: {
                            display: 'flex',
                            height: '26px',
                            width: '26px',
                            backgroundColor: 'rgba(104, 79, 52, 1)',
                            color: '#fff',
                            fontSize: '17px',
                            justifyContent: 'center',
                            alignItems: 'center',
                            borderRadius: '4px',
                        }
                    }"
                />
                <big-button
                    @click="goWithdraw"
                    class="go-withdraw scale-infinite"
                >下载提现</big-button>
                <div
                    class="footer"
                    @click="setDownloadTip(true)">？下载不了点这里</div>
            </div>
            <div
                v-if="wechatLoginDialog"
                class="wechat-dialog">
                <div
                    class="wechat-login-dialog"
                >
                    <i
                        @click="goSJQQ"
                        class="wechat-dialog-close"/>
                    <p>微信登录后可立即提现</p>
                    <div
                        @click="wehcatAuth('dialogLogin')"
                    >去登录</div>
                </div>
            </div>
            <div
                v-if="noWithdrawTipDialog"
                class="no-withdraw-tip-dialog">
                <div>
                    <i
                        class="no-withdraw-tip-logo"/>
                    <p class="no-withdraw-tip-dialog-title">温馨提示</p>
                    <p class="no-withdraw-tip-dialog-text">
                        提现需要下载安装“想看APP”，如果您现在依然无法下载，请到您手机中的应用商店<span>搜索“想看”进行安装</span></p>
                    <p class="no-withdraw-tip-dialog-shop">
                        常见的应用商店有：应用宝、360手机助手、PP手机助手、小米应用商店、华为应用市场、oppo应用商店、vivo应用商店等</p>
                    <div
                        @click="hideNoWithdrawTipDialog"
                        class="no-withdraw-tip-hide"
                    >知道了</div>
                </div>
            </div>
            <div
                class="browser-open-dialog"
                v-if="browserOpenDialog"
                @click="hideBrowserOpenDialog"
            />
        </div>
        <gray-mask
            :show="downloading"
            @click="setDownloading(false)">
            <div class="download">
                <div class="download-top"/>
                <div class="download-title">正在前往官方应用市场下载…</div>
                <div class="download-tips">若跳转失败</div>
                <div class="download-tips">请选择浏览器打开</div>
                <div class="download-browser" />
            </div>
        </gray-mask>
        <gray-mask :show="showDownloadTip">
            <div class="download-tip">
                <p class="download-tip__title">如你无法下载请到</p>
                <p class="download-tip__title">手机应用商店或手机下载助手</p>
                <p class="download-tip__des">搜索“想看”下载安装</p>
                <div class="download-tip__logo"/>
                <div
                    class="download-tip__btn"
                    @click="setDownloadTip(false)">知道了</div>
            </div>
        </gray-mask>
        <gray-mask
            :style="{background:'rgba(0, 0, 0, 0.8)'}"
            :show='pastDueModal'>
            <div>
                <div class="pastDue">
                    该链接已过期，请退出重新扫码～
                </div>
            </div>
        </gray-mask>

    </div>
</template>

<script>
import { invitationShare, invitationPreinvited } from '@/api/invitation';
import CountDown from '@/components/CountDown';
import GrayMask from '@/components/GrayMask';
import { getShareChannel, goBrowerDownload } from '@/utils/share';
import { isWechat } from '@/utils/ua';
import { copy } from '@/utils/string';
import { set, get } from '@/utils/cache';
import SwipeList from './components/swipe-list';
import BigButton from './components/big-button';


export default {
    name: 'Sharecode',
    components: {
        SwipeList,
        BigButton,
        CountDown,
        GrayMask,
    },
    data() {
        return {
            data: {},
            phonenumber: '',
            phonenumberError: false,
            invitationCode: this.$route.query.shareId || '',
            preinvited: this.$route.query.preinvited === '1',
            wechatLoginDialog: false,
            noWithdrawTipDialog: false,
            noWithdrawTip: false,
            browserOpenDialog: false,
            downloading: false,
            showDownloadTip: false,
            telBtnActive: false, // 手机号输入框下面的按钮是否闪烁
            headIcon: 'https://ts.market.mi-img.com/download/Browser/05c8b647588f540a93348bd68816ff64149a83f74/default-icon.png',
            pastDueModal: false,
        };
    },
    watch: {
        preinvited(val) {
            if (val) {
                // 进入可提现页面后3秒自动点击
                setTimeout(() => {
                    if (!this.downloading) {
                        this.download();
                    }
                }, 3000);
            }
        },
    },
    computed: {
        showInvitationCode() {
            return this.invitationCode.replace(/^.*?#/, '');
        },
        channelName() {
            return (this.invitationCode.indexOf('#') > -1) ? this.invitationCode.replace(/#\w+/, '') : '0';
        },
        shareChannel() {
            const channel = getShareChannel(this.$route.query.share);
            if (channel) {
                return channel;
            }
            if (this.$route.query.share === 'face2face') {
                return '面对面';
            }
            if (this.showInvitationCode === 'XIANGKAN') {
                return '分享页';
            }
            return 'Unkown-Channel';
        },
    },
    beforeCreate() {
        if (isWechat() && !this.$route.query.token) {
            this.$router.replace({
                name: 'invitationNewShare',
                query: this.$route.query,
            });
        }
        window.document.title = '跟我一起领红包！- 想看资讯 - 想看世界，想看你！';
    },
    created() {
        set('clickGoWithdraw', 0);

        invitationShare({ invitationCode: this.$route.query.shareId }).then((res) => {
            if (res.data.status === 200) {
                this.data = res.data.data;

                if (this.$route.query.code) {
                    this.submitPreinvited();
                }

                this.$root.hidePlaceholder();
            } else {
                this.$root.hidePlaceholder();
                this.goError();
            }
        }).catch(() => {
            this.$root.hidePlaceholder();
            this.goError();
        });

        if (this.$route.query.preinvited === '1') {
            this.$o2o({
                e_c: '分享页',
                e_a: '曝光',
                e_n: '领取成功页',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
        } else {
            this.$o2o({
                e_c: '分享页',
                e_a: '曝光',
                e_n: '邀请码分享页面',
                e_x: {
                    source: this.$route.query.source,
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
        }
    },
    methods: {
        telInputStatusHandler(isFoucs) {
            if (isFoucs) {
                this.$refs.phonenumber.type = 'number';
                this.phonenumber = '';
                this.phonenumberError = false;
                this.$refs.phonenumber.style.color = '#000';

                if (this.$refs.phonenumber.scrollIntoView) {
                    this.$refs.phonenumber.scrollIntoView();
                }

                if (this.$refs.phonenumber.scrollIntoViewIfNeeded) {
                    this.$refs.phonenumber.scrollIntoViewIfNeeded();
                }

                if (this.$refs.phoneLogin) {
                    this.$refs.phoneLogin.style.bottom = '29px';
                }
                this.telBtnActive = false;
            } else {
                this.telBtnActive = this.phonenumber.length === 11;
            }
        },
        setDownloadTip(val) {
            this.showDownloadTip = val;
            if (val) {
                this.$o2o({
                    e_c: '分享页',
                    e_a: '点击',
                    e_n: '下载不了点这里',
                    e_x: {
                        share_channel: this.shareChannel,
                        channel_name: this.channelName,
                    },
                });
                this.$o2o({
                    e_c: '分享页',
                    e_a: '曝光',
                    e_n: '温馨提示页',
                    e_x: {
                        share_channel: this.shareChannel,
                        channel_name: this.channelName,
                    },
                });
            }
        },
        setDownloading(val) {
            this.downloading = val;
            if (val) {
                this.$o2o({
                    e_c: '分享页',
                    e_a: '曝光',
                    e_n: '微信跳浏览器弹窗',
                    e_x: {
                        share_channel: this.shareChannel,
                        channel_name: this.channelName,
                    },
                });
            }
        },
        goError() {
            this.$error(this.$route.name);
        },
        setWecahtPreinvited(flag) {
            const path = this.$route.path;
            const query = this.$route.query;

            const location = {
                path,
                query: Object.assign({}, query, { preinvited: flag, wechat: flag }),
            };

            this.$router.replace(location);
        },
        hideBrowserOpenDialog() {
            this.browserOpenDialog = false;
        },
        showBrowserOpenDialog() {
            this.$o2o({
                e_c: '分享页',
                e_a: '曝光',
                e_n: '浏览器打开浮层',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
            this.setWecahtPreinvited(1);
            this.browserOpenDialog = true;
        },
        showNoWithdrawTipDialog() {
            this.$o2o({
                e_c: '分享页',
                e_a: '点击',
                e_n: '悬浮提示',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
            this.$o2o({
                e_c: '分享页',
                e_a: '曝光',
                e_n: '悬浮提示弹窗',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
            this.noWithdrawTipDialog = true;
        },
        hideNoWithdrawTipDialog() {
            this.$o2o({
                e_c: '分享页',
                e_a: '点击',
                e_n: '关闭悬浮提示弹窗',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
            this.noWithdrawTipDialog = false;
        },
        showNoWithdrawTip() {
            this.$o2o({
                e_c: '分享页',
                e_a: '曝光',
                e_n: '悬浮提示',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });
            this.noWithdrawTip = true;
        },
        goWithdraw() {
            this.$o2o({
                e_c: '分享页',
                e_a: '点击',
                e_n: '去提现',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });

            if (!isWechat()) {
                this.download();
                return true;
            }
            let clickGoWithdraw = parseInt(get('clickGoWithdraw'), 10);
            if (clickGoWithdraw) {
                clickGoWithdraw += 1;
            } else {
                clickGoWithdraw = 1;
            }
            set('clickGoWithdraw', clickGoWithdraw);

            // if (clickGoWithdraw === 1) {
            //   this.showBrowserOpenDialog();
            //   return true;
            // } else if (clickGoWithdraw === 2) {
            //   this.showNoWithdrawTip();
            //   this.showBrowserOpenDialog();
            //   return true;
            // }

            this.download();
            return true;
        },
        goSJQQ() {
            this.download();
        },
        wehcatAuth(type) {
            this.copy();
            if (!this.$route.query.code) {
                this.$o2o({
                    e_c: '分享页',
                    e_a: '点击',
                    e_n: type === 'dialogLogin' ? '去登陆' : '微信登陆',
                    e_x: {
                        share_channel: this.shareChannel,
                        channel_name: this.channelName,
                    },
                });
                setTimeout(() => {
                    if (!this.wechatLoginDialog) {
                        this.$o2o({
                            e_c: '分享页',
                            e_a: '曝光',
                            e_n: '未授权弹窗',
                            e_x: {
                                share_channel: this.shareChannel,
                                channel_name: this.channelName,
                            },
                        });
                    }
                    this.wechatLoginDialog = true;
                }, 1000);

                location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx64f017709ff9fc6e&redirect_uri=${encodeURIComponent(location.href)}&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect`;
            } else {
                this.submitPreinvited();
            }
        },
        submitPreinvited() {
            invitationPreinvited({
                invitationCode: this.$route.query.shareId,
                type: 'wechat',
                key: this.$route.query.code,
                channel: this.$route.query.share,
                token: this.$route.query.token,
            }).then((res) => {
                if (res.data.status === 200) {
                    this.$o2o({
                        e_c: '分享页',
                        e_a: '曝光',
                        e_n: '领取成功页',
                        e_x: {
                            share_channel: this.shareChannel,
                            channel_name: this.channelName,
                        },
                    });
                    this.preinvited = true;
                } else if (res.data.status === 500) {
                    if (res.data.data && res.data.data.code === 6) {
                        this.pastDueModal = true;
                    } else {
                        this.download();
                    }
                } else {
                    this.download();
                }
            }).catch(() => {
                this.download();
            });
        },
        submitPhoneNumber() {
            this.$o2o({
                e_c: '分享页',
                e_a: '点击',
                e_n: '立即领取',
                e_x: {
                    share_channel: this.shareChannel,
                    channel_name: this.channelName,
                },
            });

            const regexp = /^1\d{10}$/;

            if (!regexp.test(this.phonenumber)) {
                this.$refs.phonenumber.type = 'text';
                this.phonenumber = '请输入正确的手机号码';
                this.phonenumberError = true;
                this.$refs.phonenumber.style.color = 'rgba(255, 46, 40, 1)';
                return false;
            }

            this.copy();
            invitationPreinvited({
                invitationCode: this.$route.query.shareId,
                type: 'phonenumber',
                key: this.phonenumber,
            }).then((res) => {
                if (res.data.status === 200) {
                    this.$o2o({
                        e_c: '分享页',
                        e_a: '曝光',
                        e_n: '领取成功页',
                        e_x: {
                            share_channel: this.shareChannel,
                            channel_name: this.channelName,
                        },
                    });
                    this.preinvited = true;
                } else {
                    if (res.data.data.code === 2) {
                        this.$refs.phonenumber.type = 'text';
                        this.phonenumber = '请输入正确的手机号码';
                        this.phonenumberError = true;
                        this.$refs.phonenumber.style.color = 'rgba(255, 46, 40, 1)';

                        return;
                    }
                    this.download();
                }
            }).catch(() => {
                this.download();
            });
            return true;
        },
        isWechatBrowser() {
            return isWechat() || this.$route.query.wechat === '1';
        },
        copy() {
            let data = this.$route.query.shareId;

            data = data.replace(/^.*?#/, '');

            if (this.isWechatBrowser()) {
                data += ',WECHAT';
            } else {
                data += ',BROWSER';
            }

            copy(data);
        },
        download() {
            // 微信环境需要弹引导窗
            this.setDownloading(isWechat());

            goBrowerDownload({
                page: 'invitation',
                source: 'invitation',
                channel: 'xiangkan_invitation.apk',
                schema: this.$route.query.schema,
            });
            /* goApp(
                'bikan://goto/newsTab',
                `${window.location.origin}/api/v1/package/xiangkan_invitation.apk`,
                { ckey: 'CK1429645648411' },
            ); */
        },
        getShortName(v) {
            if (typeof v === 'string') {
                return v.length > 5 ? `${v.substr(0, 4)}...` : v;
            }
            return '';
        },
    },
};
</script>

