<template>
    <div
        class="countdown"
        :style="CStyle"
        v-if="countDownTime >= 0"
    >
        <template v-if="isShowDay">
            <span v-if="days >= 0">
                <span :style="CStyle.time">{{ ('0' + days).slice(-2) }}</span> 天
            </span>
        </template>
        <span v-if="hours >= 0">
            <span :style="CStyle.time">{{ ('0' + hours).slice(-2) }}</span>
            <template v-if="separator">{{ separator }}</template><template v-else> 时</template>
        </span>
        <span v-if="minutes >= 0">
            <span :style="CStyle.time">{{ ('0' + minutes).slice(-2) }}</span>
            <template v-if="separator">{{ separator }}</template><template v-else> 分</template>
        </span>
        <span v-if="seconds >= 0">
            <span :style="CStyle.time">{{ ('0' + seconds).slice(-2) }}</span>
            <template v-if="!separator"> 秒</template>
        </span>
    </div>
</template>

<script>
export default {
    name: 'CountDown',
    props: {
        CStyle: { type: Object, default: () => ({ time: {} }) },
        time: { type: Number, required: true, default: 7 * 24 * 60 * 60 * 1000 },
        isShowDay: { type: Boolean, default: true },
        isStop: { type: Boolean, default: false },
        separator: { type: String, default: '' },
    },
    data() {
        return {
            D: 24 * 60 * 60 * 1000,
            H: 60 * 60 * 1000,
            M: 60 * 1000,
            S: 1000,
            intervalId: 0,
            countDownTime: 0,
            previousTime: Date.now(),
        };
    },
    computed: {
        days() {
            const h = Math.floor(this.countDownTime / this.D);

            return h >= 0 ? h : 0;
        },
        hours() {
            const h = Math.floor((this.countDownTime - (this.days * this.D)) / this.H);

            return h >= 0 ? h : 0;
        },
        minutes() {
            const m = Math.floor((this.countDownTime - (this.days * this.D)
            - (this.hours * this.H)) / this.M);

            return m >= 0 ? m : 0;
        },
        seconds() {
            const s = Math.floor((this.countDownTime - (this.days * this.D)
            - (this.hours * this.H) - (this.minutes * this.M)) / this.S);

            return s >= 0 ? s : 0;
        },
    },
    watch: {
        time: {
            handler(newVal) {
                if (newVal) {
                    setTimeout(() => {
                        this.countDownTime = newVal;
                        this.intervalId = setInterval(() => this.tick(), 1000);
                    }, 0);
                } else {
                    clearInterval(this.intervalId);
                }
            },
            immediate: true,
        },
    },
    destoryed() {
        clearInterval(this.intervalId);
    },
    methods: {
        tick() {
            const now = Date.now();
            const countDownTime = (now - this.previousTime);
            this.countDownTime = this.countDownTime - countDownTime;
            this.previousTime = now;

            if (this.isStop) {
                if ((this.countDownTime < 1000 && this.countDownTime > 0)
                    || (this.countDownTime < 0 && this.countDownTime > -1000)) {
                    this.$emit('stop');
                    clearInterval(this.intervalId);
                }
            }

            if (this.countDownTime < 0) {
                this.countDownTime = 0;
            }
        },
    },
};
</script>

<style scoped>
.countdown {
    font-size: 12px;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 16px;
}

.countdown > span {
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.countdown > span > span {
    width: 18px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 2px;
}
</style>

